<template>
  <div>
    <div class="w-100 mt-5 mb-5">
      <div class="notQR">
        <div
          class="notQRBox"
          style="border: 1px solid #5c5c5c; border-radius: 10px; overflow: auto"
        >
          <div class="mt-3 p-4 font-weight-bold" style="height: 10%">
            <div class="d-flex">
              <p class="ml-3">{{ $t("") }}</p>
            </div>
            <hr />
          </div>
          <div class="show" style="height: 80%">
            <div class="mt-4 pl-4 pr-4 h-100">
              <div class="formInside">
                <p>{{ $t(" ") }}</p>
                <form>
                  <input
                    type="password"
                    class="illBtn"
                    autocomplete="off"
                    v-model="pw"
                  />
                </form>
                <div class="mt-3">
                  <small>{{ $t(" 4 ") }}</small
                  ><br />
                  <small>{{
                    $t("    ")
                  }}</small
                  ><br />
                  <small>{{ $t("    ") }}</small
                  ><br />
                  <small>{{
                    $t("      ")
                  }}</small>
                  <br />
                </div>
              </div>
              <div class="onSubmit">
                <button class="illBtn" @click.prevent="onSubmit">
                  {{ $t("") }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

const instance = axios.create({
  baseURL: "http://localhost:9000",
});
export default {
  data() {
    return {
      pw: "",
    };
  },
  beforeMount() {
    if (this.$store.state.Info.qrlist === "") {
      alert(this.$t(" ."));
      this.$router.push("/");
    }
  },
  methods: {
    onSubmit() {
      if (this.pw === "") {
        alert(this.$t(" ."));
      } else {
        let formData = new FormData();
        formData.append("pw", this.pw);
        formData.append("qrlist", this.$store.state.Info.qrlist);
        instance.post("/resetN", formData).then((res) => {
          try {
            if (res.data === false) {
              alert(this.$t("  . ."));
            } else {
              instance.post("/reset", formData).then((res2) => {
                if (res2.data === true) {
                  this.$router.push("/");
                }
              });
            }
          } catch (err) {
            console.log(err);
          }
        });
      }
    },
  },
};
</script>

<style>
.backimg {
  width: 25px;
  height: 25px;
}
.illBtn {
  width: 100%;
  height: 40px;
  margin-top: 8px;
  border: 1px solid #a9a9a9;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.onSubmit {
  height: 60%;
  display: flex;
  align-items: flex-end;
}
.formInside {
  height: 40%;
}
</style>
